import React, { Component } from "react";
import { getCetakUlang } from "../../actions/cetakUlangAction";
import { getUUID } from "../../actions/fakturAction";
import { getsupplierList } from "../../actions/supplierAction";
import Axios from "axios";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import notrespond from "../../Images/500.svg";
import Skeleton from "react-loading-skeleton";
import { Link } from "react-router-dom";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;
const server = process.env.REACT_APP_BACKEND_URL;
class FormLaporanPenerimaanSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "stok",
          text: "Stok",
        },
        {
          dataField: "namaSupplier",
          text: "noNota",
        },
        {
          dataField: "bruto",
          text: "Bruto(Gr)",
        },
        {
          dataField: "netto",
          text: "Netto",
        },
        {
          dataField: "rp",
          text: "Ongkos(Rp)",
        },
      ],
      data: [
        {
          tanggal: "2020-11-12",
        },
      ],
      listPenerimaan: "ada",
    };
  }
  componentDidMount() {
    this.props.dispatch(getsupplierList());
    Axios.get(server + "suppliers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch((err) => alert(err));
    this.props.dispatch(getUUID());
    this.props.dispatch(getCetakUlang());
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };
  render() {
    return (
      <>
        <div className="container-fluid">
          <ol className="breadcrumb mb-2">
            <Link to="/cetakUlangBukti">
              <button
                type="button"
                className="btn btn-secondary"
                onClick={() => localStorage.clear()}
              >
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="card">
            <div className="card-body">
              <h2>Cetak Ulang Butki Penerimaan Supplier</h2>
              <br />
              <div className="row">
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="">Tanggal Awal</label>
                  <input
                    type="date"
                    id="tanggal"
                    name="tanggalAwal"
                    className="form-control"
                    defaultValue={this.state.startingDate}
                    onChange={this.inputChange("tanggalAwal")}
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <div className="text-center">
                    <i className="fas fa-exchange-alt"></i>
                  </div>
                </div>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="">Tanggal Akhir</label>
                  <input
                    type="date"
                    id="tanggal"
                    name="tanggalAkhir"
                    className="form-control"
                    defaultValue={this.state.startingDate}
                    onChange={this.inputChange("tanggalAkhir")}
                  />
                </div>
                <div className="col-lg-4 col-md-4 mt-3">
                  <label htmlFor="">Supplier</label>
                  <select
                    name="supplier"
                    id="kodeSupplier"
                    className="form-control"
                    onChange={this.pilihSupplier("kodeSupplier")}
                  >
                    <option value="DEFAULT | DEFAULT">SILAHKAN PILIH</option>
                    {this.state.listSupplier.map((kode) => (
                      <option
                        value={kode.kodeSupplier + "|" + kode.namaSupplier}
                      >
                        {kode.namaSupplier}
                      </option>
                    ))}
                  </select>
                </div>
                <div className="col-md-6 col-lg-4 mt-3">
                  <label htmlFor="">Nomor Terima</label>
                  <input
                    type="text"
                    id="noTerima"
                    name="noTerima"
                    className="form-control"
                    onChange={this.inputChange("noTerima")}
                  />
                </div>
                <div className="col-md-6 col-lg-4 mt-3">
                  <label htmlFor="">Nomor Nota</label>
                  <input
                    type="text"
                    id="noNota"
                    name="noNota"
                    className="form-control"
                    onChange={this.inputChange("noNota")}
                  />
                </div>
                <div className="col-md-12 col-lg-12 mt-3">
                  <div className="row">
                    <div className="col-lg-6 col-md-6">
                      <div className="text-left">
                        {/* <Link to="/ViewPDF">
                              <button className="btn btn-primary" type="button">
                                PDF
                              </button>
                            </Link> */}
                      </div>
                    </div>
                    <div className="col-lg-6 col-md-6">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          data-toggle="modal"
                          data-target="#primary-header-modal"
                        >
                          <i className="fa fa-search mr-1"></i>
                          Cari Data
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-12">
                      {this.state.listPenerimaan ? (
                        <ToolkitProvider
                          bootstrap4
                          keyField={
                            this.props.namaKey ? this.props.namaKey : "kode"
                          }
                          data={this.state.data}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="col-lg-12 col-md-12 mt-5">
                              <div className="text-right">
                                <SearchBar
                                  {...props.searchProps}
                                  className="form-control"
                                  placeholder="Tuliskan Kode / Nama"
                                  style={{ margin: 0 }}
                                />
                              </div>
                              <BootstrapTable
                                {...props.baseProps}
                                headerClasses="header-table"
                                striped
                                hover
                                bordered={true}
                                pagination={paginationFactory()}
                              />
                              <ExportCSVButton
                                {...props.csvProps}
                                className="btn btn-primary"
                              >
                                Export CSV
                              </ExportCSVButton>
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <div>
                          {this.props.error ? (
                            <div className="text-center">
                              <img
                                src={notrespond}
                                style={{ width: "30%" }}
                                alt="NOT RESPOND"
                              />
                              <br />
                              <h4>Request API Gagal, Silahkan Refresh</h4>
                            </div>
                          ) : (
                            <Skeleton width="100%" height={180} />
                          )}
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default connect()(FormLaporanPenerimaanSupplier);
